body {
    height: 100%;
    margin: 0;
    font-family: 'Roboto';
    margin: 0;
    background-color: rgb(53, 69, 77);
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  
  .netlab-navbar {
    background-color: rgb(53, 69, 77);
    height: 5em;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    flex-shrink: 0;
    
  }
  
  .netlab-footer {
    background-color: rgb(53, 69, 77);
    height: 15em;
    display: flex;
    flex-direction: row;
    flex-shrink: 0;
  }
  
  .div-footer-info {
    flex: 2;
    font-size: 1em;
    color: white;
    display: flex;
    flex-direction: row;
    padding: 2em;
  }
  
  .address-info {
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: center;
  }
  .address-info-text {
    margin: 0.2em;
  }
  
  .div-footer-widgets {
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: start;
    justify-content: end;
    padding:2em
  }
  
  .widget-link {
    height: 2em;
    margin: 0.5em
  }
  
  .widget-image {
    object-fit: contain;
    height: 100%;
  }
  
  .hero {
    width: 100%;
    min-height: 15em;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    position: relative;
    background: white;
    font-family: 'Barlow Condensed';
  }
  
  .hero-image {
    opacity: 0.8;
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: absolute;
  }

  .hero-title {
    color: white;
    margin: 0;
    font-weight: 500;
    z-index: 10;
    /*text-shadow: 
    -2px -2px 0 rgb(53, 69, 77),  
     2px -2px 0 rgb(53, 69, 77),  
    -2px  2px 0 rgb(53, 69, 77),  
     2px  2px 0 rgb(53, 69, 77);*/
    font-size: clamp(3rem, 6vw, 7rem);
  }
  
  .about {
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: 'Barlow Condensed';
    padding: 2em;
  }
  
  .about-title {
    font-size: 4em;
    margin: 0;
    font-weight: 500;
  }
  
  .navbar-links {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 2em;
    padding: 2em;
    font-family: 'Roboto';
    font-weight: 400;
  }
  
  .navbar-link {
    text-decoration: none;
    color: white;
    font-size: 1em;
  }
  
  .navbar-button {
    border: none;
    background-color: rgb(53, 69, 77);
    color: white;
    font-size: 1em;
  }
  
  .navbar-link:hover {
    cursor: pointer;
  }
  
  .navbar-button:hover {
    cursor: pointer;
  }
  
  .a-navbar {
    height: 80%;
  }
  
  .banner-navbar {
    object-fit: contain;
    height: 100%;
    margin-left: 3.5em;
    filter: invert()
  }
  
  
  .banner-hero {
    width: 50%;
    object-fit: contain;
  }
  
  .div-hero-banner {
    flex: 1;
    padding: 5vw;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }
  
  .div-hero-link {
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      position: relative;
      min-height: 20em;
  }
  
  .hero-link {
    width: 60%;
    color: white;
    font-weight: 400;
    font-size: 1.7vw;
    background-color: #337F89;
    border-radius: 10px;
    padding: 1.7vh;
    margin: 0.8vh;
    text-decoration: none;
    text-align: center;
  }
  
  .hero-link:hover {
    background-color: rgb(53, 69, 77);
    cursor: pointer;
  }
  
  .container {
    display: flex;
    flex-flow: column;
    height: 100vh;
  }
  
  .box-container {
    flex: 1 1 auto;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 2em;
    min-height: min-content;
  }

.about-image {
  width: auto;
  max-height: 20rem;
  height: 80%;
  width: 40%;
  margin-right: 3.5em;
  margin-left: 3.5em;
}

.about-img {
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.downloads {
  background-color: white;
  display: flex;
  flex-direction: column;
  font-family: 'Barlow Condensed';
  padding: 2em;
  height: 100%;
  width: 100%;
}

.download-div {
  background-color: rgba(53, 69, 77, 0.517);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Barlow Condensed';
  padding: 1.5em;
  height: 10vh;
  min-height: 5em;
  width: 20vw;
  min-width: 10em;
  border-radius: 1em;
}